import React, { useEffect, useCallback } from 'react';
import marked from 'marked';
import hljs from 'highlight.js/lib/highlight';
import javascript from 'highlight.js/lib/languages/javascript';
import css from 'highlight.js/lib/languages/css';
import scss from 'highlight.js/lib/languages/scss';
import shell from 'highlight.js/lib/languages/shell';
import typescript from 'highlight.js/lib/languages/typescript';
import { limitImgSize, insertCopyIcon } from 'utils';
import './index.scss';
hljs.registerLanguage('javascript', javascript);
hljs.registerLanguage('css', css);
hljs.registerLanguage('scss', scss);
hljs.registerLanguage('sh', shell);
hljs.registerLanguage('ts', typescript);
marked.setOptions({
  renderer: new marked.Renderer(),
  gfm: true,
  pedantic: false,
  sanitize: false,
  tables: true,
  breaks: true,
  smartLists: true,
  smartypants: true,
  highlight(code) {
    return hljs.highlightAuto(code).value;
  },
});
interface IProps {
  code: string;
}
const Markdown = ({ code }: IProps) => {
  const markedString = marked(code);
  const setLinkTarget = useCallback(() => {
    let links = document.querySelectorAll('.markdown-container a');
    for (let i = 0; i < links.length; i++) {
      let link = links[i] as HTMLAnchorElement;
      link.setAttribute('target', '_blank');
    }
  }, []);
  useEffect(() => {
    limitImgSize();
    insertCopyIcon();
    setLinkTarget();
  }, [code, setLinkTarget]);
  return (
    <div className={'markdown-container'}>
      <div dangerouslySetInnerHTML={{ __html: markedString }}></div>
    </div>
  );
};
export default Markdown;
